<template>
  <form action="/User/Login" method="POST">
    <div class="loginkuang">
      <div class="logininskuang">
        <h1>{{ message }}</h1>

        <p><span class="loginred">*</span></p>

        <el-input
          type="text"
          v-model="userlogin.emailcode"
          placeholder=""
          name="email"
          id="loginuser"
          ref="InputUserRef"
        ></el-input>
        <br />
        <div id="warn">
          <p for="user" v-if="userlogin.emailcode"></p>
          <p for="user" class="loginred" v-else></p>
        </div>

        <p><span class="loginred">*</span></p>

        <el-input
          type="password"
          v-model="userlogin.passwordstring"
          placeholder=""
          name="password"
          id="loginpassword"
        ></el-input>
        <div id="warn">
          <p for="password" v-if="userlogin.passwordstring"></p>
          <p for="password" class="loginred" v-else></p>
        </div>
        <br />
        <br />
        <div class="buttonlogin">
          <el-button type="primary" @click="login()"></el-button>
          <el-button type="text" @click="register()"></el-button>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import event from "../../static/config/event.js";
export default {
  data() {
    return {
      message: "",
      userlogin: {
        emailcode: "",
        passwordstring: "",
      },
    };
  },
  methods: {
    register() {
      this.$router.push({
        path: "./register",
      });
      //console.warn('')
      //console.error('')
    },
    /* rigister(){
        axios.get('/User/Rigister').then(res =>{
          console.log(res)
        })
      }, */

    async login() {
      // 
      if (this.userlogin.emailcode === "")
        return this.$message.error({
          message: "",
          showClose: true,
        });
      else if (this.userlogin.passwordstring === "")
        return this.$message.error({
          message: "",
          showClose: true,
        });
      else if (this.userlogin.passwordstring.length < 6)
        return this.$message.error({
          message: "6",
          showClose: true,
        });
      else if (
        this.userlogin.emailcode.length < 6 ||
        this.userlogin.emailcode === ""
      )
        return this.$message.error({
          message: "6",
          showClose: true,
        });
      await this.$axios({
        method: "post",
        url: "/api/User/Login",
        /* params :{
                  email:this.userlogin.emailcode,
                  password:this.userlogin.passwordstring,
                },  */
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        data: {
          email: this.userlogin.emailcode,
          password: this.userlogin.passwordstring,
        }, //jsondata 
      })
        .then((res) => {
          // console.log(res.data);
          if (res.data.code == 200) {
            event.$emit("my-Fun", res.data.user),
              // console.log(res.data.user),
              // 
              sessionStorage.setItem("username", JSON.stringify(res.data.user));
            sessionStorage.setItem("code", JSON.stringify(res.data.code));
            // 
            // localStorage.setItem("username", JSON.stringify(res.data.user)),
            // localStorage.setItem("code", JSON.stringify(res.data.code)),
            this.$message.success({
              message: res.data.msg,
              showClose: true,
            }),
              this.$router.push({
                path: "./Home",
              });
          }
          /* setTimeout()  (, millisecond),, ,  setTimout(,4000)*/
          // console.log(res.data.user),
          //myFun $emitres.data.user
          //
          else if (res.data.code == 500) {
            this.$message.error({
              message: res.data.msg,
              showClose: true,
            });
          } else if (res.data.code == 400) {
            this.$message.error({
              message: res.data.msg,
              showClose: true,
            });
          } else if (res.data.code == 201) {
            this.$message.success({
              message: res.data.msg,
              showClose: true,
            }),
              event.$emit("my-Fun", res.data.user),
              sessionStorage.setItem("username", JSON.stringify(res.data.user));
            sessionStorage.setItem("code", JSON.stringify(res.data.code));
            // localStorage.setItem("username", JSON.stringify(res.data.user)),
            // localStorage.setItem("code", JSON.stringify(res.data.code)),
            this.$router.push({
              path: "./admin",
            });
          } else if (res.data.code == 401) {
            this.$message.error({
              message: res.data.msg,
              showClose: true,
            });
          } else
            this.$message.error({
              message: res.data.msg,
              showClose: true,
            });
        })
        .catch((error) => alert("")); //
    },
  },
  mounted() {
    //JSdom
    // document.getElementById('loginuser').focus()
    //refs
    this.$refs.InputUserRef.focus();
  },
};
</script>

<style scoped>
#warn {
  font-size: 11px;
  position: absolute;
  width: 300px;
  height: 18px;
  text-align: right;
}

.loginred {
  color: red;
}

.loginkuang {
  height: 400px;
  width: 400px;
  margin: auto;
  margin-top: 80px;
  padding: 20px;
}

.logininskuang {
  position: relative;
  height: 300px;
  width: 300px;
  margin: auto;
  margin-top: 30px;
  padding: 20px;
}

.buttonlogin {
  text-align: center;
  float: left;
}
</style>



