<script>
  import Home from './Home.vue'
  import tarbartemplate from './Appcomponets/tarbartemplate.vue'
  import event2 from "../../static/config/event.js"
  import loginitem from './Appcomponets/loginitem.vue'
  import backup from './Appcomponets/backup.vue'
  export default {
    name: 'Goodfalls',
    components: {
      tarbartemplate,
      Home,
      loginitem,
      backup,
    },
    data() {
      return {
        msg: '',
        htmlbt: '',
        tarshow: true,
        eleSelector: '',
      }
    },
    methods: {
      backTop(){},
      status () {
        console.log(sessionStorage.getItem('username'))
        if (sessionStorage.getItem('username') === '"admin"'){
          this.tarshow = false
        } else{
          this.tarshow = true
        }
      },
      logoutstatus(value){
        this.tarshow =value
      },
      handleSelect() {
        console.log("handleSelect")
      },
    },
    created(){
      event2.$on('myfun',ab=>{
        if (ab ==200){
          // console.log(this.tarshow),
          //
          this.tarshow =false
          }
        else if(ab==400){
          this.tarshow =true
          }
        }),
        this.status()
