<template>
  <!-- -->
  <el-dropdown @command="handleCommand">
    <span class="el-dropdown-link">
      <i class="el-icon-arrow-down el-icon--right"></i>
    </span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item command="a"></el-dropdown-item>
      <el-dropdown-item command="b">boss</el-dropdown-item>
      <el-dropdown-item command="c"></el-dropdown-item>
      <el-dropdown-item command="d"></el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>
<style scoped>
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }

  .el-icon-arrow-down {
    font-size: 12px;
  }
  a{
    text-decoration:none;
    cursor: pointer;
    color: #409EFF;
  }

</style>

<script>
  export default {
    methods: {
      handleCommand(command) {
        if (command == 'a')
          // this.$message(' ' + command+ '');
          this.$message(' ' + ''+ ''),
          setTimeout("window.open('https://hotels.ctrip.com', '_blank')",1000)

        else if (command == 'b')
          // this.$message(' ' + command+ '');
          this.$message(' ' + 'boss'+ ''),
          setTimeout("window.open('https://www.zhipin.com', '_blank')",1000)

        else if (command == 'c')
          // this.$message(' ' + command+ '');
          this.$message(' ' + ''+ ''),
          setTimeout("window.open('https://landing.zhaopin.com', '_blank')",1000)

        else
            // this.$message(' ' + command+ '');
            this.$message(' ' + ''+ ''),
            setTimeout("window.open('https://wow.liepin.com', '_blank')",1000)

      }
    }
  }
</script>



