<template>
  <el-form action="/User/Rigister" method="POST" @submit.prevent="submit" >
    <div class="registerkuang">
      <div class="registerinkuang" ref="">
        <h1>{{message}}</h1>
        <p><span class="loginred">*</span></p>
        <el-input type="text" ref="InputUserRef" v-model="rigister.userstring" placeholder="" name="user"></el-input>
        <br>
        <div id="warn">
          <p for='user' v-if="rigister.userstring"></p>
          <p for='user' class="loginred" v-else></p>
        </div>

        <p><span class="loginred">*</span></p>
        <el-input type="password" v-model="rigister.passwordstring" placeholder="" name="password"></el-input>
        <div id="warn">
          <p for='password' v-if="rigister.passwordstring"></p>
          <p for='password' class="loginred" v-else></p>
        </div>
        <p><span class="loginred">*</span></p>
        <el-input type="password" name="pwdcomfirm" v-model="rigister.pwdcomfirm" placeholder=""></el-input>
        <div id="warn">
          <p for='password' v-if="rigister.pwdcomfirm"></p>
          <p for='password' class="loginred" v-else></p>
        </div>
        <p><span class="loginred">*</span></p>
        <el-input type="text" name="cature" placeholder="" v-model="rigister.caturecode" style="width: 50%;">
        </el-input>
        <el-image :src=rigister.catureimg  @click="requestcature()" style="width: 150px;height: 40px;float: right;" v-loading = "loading"></el-image>
        <!-- <el-image style="width: 150px;height: 40px;float: right;">
              <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
              </div>
            </el-image> -->
        <div id="warn">
          <p for='password' v-if="rigister.caturecode"></p>
          <p for='password' class="loginred" v-else></p>
        </div>
        <p><span class="loginred">*</span></p>
        <el-input type="text" name="email" placeholder="" v-model="rigister.emailcode" @keyup.enter="register()"></el-input>
        <div id="warn">
          <p for='user' v-if="rigister.emailcode"></p>
          <p for='user' class="loginred" v-else></p>
        </div>
        <br>
        <br>
        <div class="buttonregister">
          <el-button type="primary" @click="register()"></el-button>
        </div>


      </div>

    </div>
  </el-form>

</template>

<script>

  export default {
    data() {
      return {
        message: '',
        rigister: {
          userstring: '',
          passwordstring: '',
          emailcode: '',
          caturecode: '',
          pwdcomfirm: '',
          catureimg: '',
        },
        loading:true,
      }
    },

    methods: {
      requestcature() {
        this.$axios({
          method: 'get',
          url: '/api/User/GetCaptcha',
        }).then(res => {
          if (res.data == '')
            return this.$message.error({
              message: '',
              showClose: true,
            })
          else
            this.rigister.catureimg = res.data
            this.loading = false
          //console.log(this.catureimg)
        }).catch((err)=>{
          console.log('')
        })
      },

      async register() {
        let _this = this
        if (_this.rigister.userstring === '' || _this.rigister.passwordstring === ''){
          return this.$message.error({
            message: '',
            showClose: true
          })
        }
        else if (_this.rigister.passwordstring.length < 6){
          return this.$message.error({
            message: '6',
            showClose: true
          })
        }
        /* else if(this.passwordstring !== this.pwdcomfirm )
             return  Message.error({message:'',showClose:true}) */
        else if (_this.rigister.caturecode.length != 4 || _this.rigister.caturecode === ''){
          return this.$message.error({
            message: '4',
            showClose: true
          })
          }
        else if (_this.rigister.emailcode.length < 5 || _this.rigister.emailcode === ''){
          return this.$message.error({
            message: '5',
            showClose: true
          })
          }
        /* {{passwordstring}}
        {{emailcode}}
        {{pwdcomfirm}}
        {{userstring}}
        {{caturecode}} */
        //newFormData ,user:  python
        //pythonjinja2user=12322&password=123456&pwdcomfirm=123456&cature=8567&email=12112334%40qq.com
        //data:{:}1 2 3
        var newFormData = new FormData()
        newFormData.append('user', _this.rigister.userstring)
        newFormData.append('password', _this.rigister.passwordstring)
        newFormData.append('email', _this.rigister.emailcode)
        newFormData.append('cature', _this.rigister.caturecode)
        newFormData.append('pwdcomfirm', _this.rigister.pwdcomfirm)

        // var formData = JSON.stringify(_this.rigister)
        await _this.$axios.post('/api/User/Rigister', newFormData
        ).then(res => {
          console.log(res.data.code)
          if (res.data.code == 200){
            this.$message.success({
              message: '',

              showClose: true,
            }),
            setTimeout(20000),
            this.$router.push({
              path: './Home'
            }) //
            }
          else if (res.data.code == 401){
            this.$message.error({
              message: res.data.msg,

              showClose: true,
            }),
            this.requestcature()
            }
          else if (res.data.code == 402){
            this.$message.error({
              message: res.data.msg,

              showClose: true,
            })
            }
          else if (res.data.code == 400){
            this.$message.error({
              message: res.data.msg,

              showClose: true,
            })
            }
          else if (res.data.code == 500){
            this.$message.error({
              message: res.data.msg,

              showClose: true,
            })
            }
            else if (res.data.code == 405){
            this.$message.error({
              message: res.data.msg,

              showClose: true,
            })
            }
        }).catch((err) => console.log(''));
        //Message.success('')

      }
    },
    //mounted 
    mounted() {
      //
      if (this.rigister.catureimg === ''){
        this.requestcature()
      }
      this.$refs.InputUserRef.focus()
    }
  }
</script>

<style scoped>
  #warn {
    font-size: 11px;
    position: absolute;
    width: 300px;
    height: 18px;
    text-align: right;
  }

  .loginred {
    color: red;
  }

  .registerkuang {
    height: 800px;
    width: 400px;
    margin: auto;
    margin-top: 80px;
    padding: 20px;
  }

  .registerinkuang {
    height: 600px;
    width: 300px;
    margin: auto;
    margin-top: 30px;
    padding: 20px;
  }

  .buttonregister {
    text-align: center;
    float: left;
  }
</style>



