import Vue from 'vue'
import Router from 'vue-router'
// import Goodfalls from '@/components/Goodfalls'
import Home from '@/components/Home'
import Second from '@/components/Second'
import Three from '@/components/Three'
import Source from '@/components/Source'
// import tarbartemplate from '@/components/Appcomponets/tarbartemplate'
import login from '@/components/login.vue'
import register from '@/components/register.vue'
import admin from '@/components/admin.vue'
import sourcedetail from '@/components/sourcedetail.vue'


Vue.use(Router)

// export default new Router({
const routes = [
  {
    path: '/',
    redirect: '/Home',
  },
  {
    path: '/login',
    name: '',
    component: login,
  },
  {
    path: '/register',
    name: '',
    component: register,
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    children:[
      
    ]
  },
  {
    path: '/Second',
    name: 'Second',
    component: Second
  },
  {
    path: '/Three',
    name: 'Three',
    component: Three
  },
  {
    path: '/Source',
    name: 'Source',
    component: Source,
    children: [
      
    ],
  },
  {
    path:'/sourcedetail',
    name:'sourcedetail',
    component: sourcedetail,
  },
  
  // {
  //   path:'/tarbartemplate',
  //   name:'tarbartemplate',
  //   component:tarbartemplate
  // },
  {
    path: '/admin',
    name: '',
    meta: {
      requireAuth: true, // 
    },
    component: admin,
  },
  {
    path: '*',
    redirect: '/login',
    hidden: true
  },
]
// })
const VueRouter = new Router({
  routes,
})
// 
const VueRouterPush = Router.prototype.push
Router.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}


// 
VueRouter.beforeEach((to, from, next) => {
  if (to.matched.some((r) => r.meta.requireAuth)) {
    let user = JSON.parse(sessionStorage.getItem('username'));
    if (user) { //  
      // console.log('', from);
      next();
    } else {
      next({
        path: '/login', //
        query: {
          redirect: to.fullPath
        },
      });
    }
  } else {
    // console.log(''); //
    next();
  }
  //  token   
  if (to.fullPath === "/login" || to.fullPath === "/Login") {
    if (sessionStorage.getItem('username')) {
      // console.log('')
      // next({
      //   path: from.fullPath
      // });
      next();
    } else {
      next();
    }
  } else if (to.fullPath === "/Home" || to.fullPath === "/home") {
    if (sessionStorage.getItem('username') === '"admin"') {
      // console.log('')
      next({
        path: from.fullPath
      });
    }
  } else if (to.fullPath === "/Second" || to.fullPath === "/second") {
    if (sessionStorage.getItem('username') === '"admin"') {
      // console.log('')
      next({
        path: from.fullPath
      });
    }
  } else if (to.fullPath === "/Three" || to.fullPath === "/three") {
    if (sessionStorage.getItem('username') === '"admin"') {
      // console.log('')
      next({
        path: from.fullPath
      });
    }
  } else if (to.fullPath === "/Source" || to.fullPath === "/source") {
    if (sessionStorage.getItem('username') === '"admin"') {
      // console.log('')
      next({
        path: from.fullPath
      });
    }
  } else if (to.fullPath === "/Register" || to.fullPath === "/register") {
    if (sessionStorage.getItem('username') === '"admin"') {
      // console.log('')
      next({
        path: from.fullPath
      });
    }
  }

})
export default VueRouter



