<template>
  <div v-if="show">
    <router-link to="/login" name="login">
      <el-button type="primary" style="border-radius: 30px"> / </el-button>
    </router-link>
  </div>
  <div v-else>
    <div class="Username" ref="UsernameRef">
      
      {{getTimeState()}} <span class="usernameclass">{{ username }}</span>
    </div>
    <el-button size="medium" type="text" @click="logout()"></el-button>
  </div>
</template>

<script>
  import event from "../../../static/config/event.js";
  export default {
    data() {
      return {
        show: true,
        username: "",
        // v-model 
      };
    },
    created: function() {
      if (this.show == true) {
        this.bbtn()
      };
    },
    methods: {
      //bbtn$emit$on()=>{  }
      bbtn: function() {
        let _this = this;
        event.$on("my-Fun", (message) => {
          //this
          _this.username = message;
          _this.show = false;
        });
      },

      // 
      logout() {
        this.$axios({
          url: "/api/User/Logout",
          method: "get",
        }).then((res) => {
          console.log("session");
          sessionStorage.clear()
          console.log("");
          localStorage.clear();
          this.$router.push("/");
          this.$message.success({
            message: res.data.msg,
            showClose: true,
          });
          this.show = true;
          this.$emit("logout", this.show);
        });
      },

      // 
      getTimeState() {
        // 
        let timeNow = new Date();
        // 
        let hours = timeNow.getHours();
        // 
        let state = ``;
        // 
        if (hours >= 0 && hours <= 4) {
          state = ` !`;
        } else if (hours > 4 && hours <= 10) {
          state = ` !`;
        } else if (hours > 10 && hours <= 14) {
          state = ` !`;
        } else if (hours > 14 && hours <= 18) {
          state = ` !`;
        } else if (hours > 18 && hours <= 24) {
          state = ` !`;
        }
        return state;
      },
    },
    mounted() {
      //[usernamecode]
      let user = JSON.parse(sessionStorage.getItem("username"))
      let code = JSON.parse(sessionStorage.getItem("code"))
      // var user = JSON.parse(localStorage.getItem("username"));
      // var code = JSON.parse(localStorage.getItem("code"))
      this.username = user;
      if (code == 200) {
        this.show = false
      } else if (code == 201) {
        this.show = false
      }
    },
  };
</script>

<style scoped>
  @import url("//unpkg.com/element-ui@2.15.7/lib/theme-chalk/index.css");

  .usernameclass {
    font-size: 16px;
    color: palevioletred;
    font-style: initial;
    font-weight: 500;
    padding: 2px;
  }

  .Username {
    /* font-size: 14px; */
    float: left;
    padding-right: 15px;
    color: grey;
  }
</style>



