<template>
  <el-card class="card-box">
    <div id="container"></div>
  </el-card>
</template>
<script>
  import AMapLoader from '@amap/amap-jsapi-loader';
  export default {
    data() {
      return {

      }
    },
    mounted() {
      this.map()
    },
    methods: {
      map() {
        AMapLoader.load({
          ///webJS API
          key: 'd3ab15847081d45882bf8b3be4030721', //loadindex.html
          version: '2.0',
          plugins: ['AMap.Scale', 'AMap.ToolBar','AMap.MapType']
        }).then((AMap) => {
          const map = new AMap.Map('container',{
            center: [109.262448,22.681257],//
            zoom: 15
          });//const map = new AMap.Map('container',{}) 
          map.addControl(new AMap.Scale())//
          // map.addControl(new AMap.MapType())
          map.addControl(new AMap.ToolBar())//
          map.add(new AMap.Marker({
            position: map.getCenter()
          }));//

        }).catch((e) => {
          console.error(e);
        });
      }
    }
  }
</script>

<style>
  .card-box {
    width: 100%;
    height: 400px;
  }
  #container {
    width: 100%;
    height: 360px;
  }
</style>



